@import url('https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background: #eee;
  width: 100vw;
  padding: 2rem 10rem;
  overflow-x: hidden;
}

header {
  margin-bottom: 1rem;
}

header p {
  font-size: 0.75rem;
}

section.items .item {
  width: 100%;
  background: white;
  padding: 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin-bottom: 2rem;
}

section.items .item .row {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin-top: 1rem;
}

section.items .item .item-field {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
section.items .item .item-field label {
  font-size: 0.6rem;
  font-weight: bold;
  margin-bottom: 0.1rem;
}

section.items .item .item-field input {
  padding: 0.25rem 0.5rem;
  /* margin-bottom: 1rem; */
}

section.items .item .item-field select {
  padding: 0.25rem 0.5rem;
  /* margin-bottom: 1rem; */
}

section.items .item .item-buttons {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
}

section.items .item .item-keywords {
  flex: 1;
}

.btn-save {
  background: #333;
  color: white;
  padding: 0.25rem 0.5rem;
  border: none;
  margin-right: 1rem;
}

.btn-save:hover {
  background: #555;
}

.btn-save:disabled {
  background: #aaa;
}

.btn-delete {
  background: red;
  padding: 0.25rem 0.5rem;
  color: white;
  border: none;
}

.btn-delete:hover {
  background: salmon;
}

.item-hide {
  display: none !important;
}

section.items .item .item-logs {
  flex: 1;
}
section.items .item .item-logs span {
  font-size: 0.75rem;
  color: white;
  padding: 0 0.5rem;
}

.btn-add {
  background: white;
  border-radius: 50%;
  height: 2.5rem;
  width: 2.5rem;
  border: none;
  font-size: 2.4rem;
  color: #f44336;
  cursor: pointer;
  position: fixed;
  top: 90vh;
  right: 1vw;
}

.btn-add :hover {
  color: salmon;
}

.fa-twitter {
  color: #1da1f2;
}

section.credits {
  position: fixed;
  top: 91vh;
  left: 1vw;
}

.fa-youtube {
  color: red;
  font-size: 2rem;
}

section.credits span {
  font-family: 'Shadows Into Light', cursive;
  font-size: 1rem;
  color: white;
}

svg {
  position: fixed;
  width: 100vw;
  top: 70vh;
  left: 0;
  z-index: -10;
}

.text-success {
  background: limegreen;
}
.text-danger {
  background: red;
}
